import React from 'react'
import ImageSection from '../Components/imageSection'
import SkillsSection from '../Components/skillssection'
import Title from '../Components/Title'

export default function AboutPage() {
    return (
        <div className="aboutpage">
            <Title title={'About Me'} span={'About Me'} />
            <ImageSection />

            <Title title={"Skills"} span={"Skills"} />
            <div className="skill-container">
                <SkillsSection skill={'Javascript'} progress={"60%"} />
                <SkillsSection skill={'Typescript'} progress={"30%"} />
                <SkillsSection skill={'React JS'} progress={"50%"} />
                <SkillsSection skill={'Node JS'} progress={"70%"} />
                <SkillsSection skill={'Python'} progress={"30%"} />
                <SkillsSection skill={'Lua'} progress={"100%"} />
                <SkillsSection skill={'C#'} progress={"69%"} />
            </div>
        </div>
    )
}
